import { useState, useEffect, memo } from "react";
import BlogsServie from "../../services/BlogsServie";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import "../../styles/validaciones.scss";
import Swal from "sweetalert2";
import FormBlog from "./FormBlog";
import { APP_DEBUG_MODE, useApi } from "../../helpers";
import { Progress } from "../../ui";

const Blogs = ({ id }) => {
    const initialMovieState = {
        id: null,
        title: "",
        content: "",
        url: "",
        visible: true
    };
    const {
        put: blogUpdate,
        isLoading: blogUpdateLoading,
        data: blogUpdateData,
    } = useApi("blogUpdate");
    const { get: blogGet, data: blogData } = useApi("blog");

    const [currentBlog, setCurrentBlog] = useState(initialMovieState);

    useEffect(() => {
        if ( blogUpdateData != null) {
            Swal.fire({
                title: "Actualizado",
                text: "El Anuncio fue actualizado con xito",
                icon: "success",
                confirmButtonText: "OK",


            });
            blogGet(`blogs/${id}`);
        }
    }, [  blogUpdateData]);



    useEffect(() => {
        if (blogData != null) {

                setCurrentBlog({
                    id: blogData.id || null,
                    title: blogData.title || "",
                    content: blogData.content || "",
                    url: blogData.url || "",
                    visible: blogData.visible || false,
                });
        }
    }, [blogData]);



    useEffect(() => {
        blogGet(`blogs/${id}`);
    }, [id]);

    const updateBlog = (data) => {

        blogUpdate(`blogs/${currentBlog.id}`, data);

    };

    return (
        <div className="submit-form">
            {!currentBlog ? (
                <div>
                    <h4>Por favor selecciona un registro</h4>
                </div>
            ) : (
                <div>
                        {blogUpdateLoading && <Progress />}
                    <Formik
                        initialValues={{
                            id: null,
                            title: "",
                            content: "",
                            url: "",
                            visible: true
                        }}
                        onSubmit={(values) => {
                            APP_DEBUG_MODE &&  console.log(values);
                            updateBlog(values);
                        }}
                        validationSchema={Yup.object({
                            title: Yup.string().required("Requerido"),
                            url: Yup.string().required("Requerido"),

                        })}
                    >
                        {(formik) => (
                            <Form>

                                <div className="modal-body">
                                    <FormBlog currentBlog={currentBlog } />
                                </div>
                                <div className="modal-footer">

                                    <button type="submit" className="btn btn-primary">Actualizar</button>
                                </div>
                            </Form>
                        )}
                    </Formik>
                </div>
            )}
        </div>
    );
};

export default memo(Blogs);
