import { APP_DEBUG_MODE } from "./constants";
import { getSessionItem } from "./functions";

const baseUrl = process.env.REACT_APP_API_URL;
export const fileUpload = async ( file ) => {
    APP_DEBUG_MODE   && console.log(file);
    const cloudUrl = baseUrl + 'files';

    const formData = new FormData();

    formData.append('image', file );

    try {
        const TOKEN = getSessionItem("token");
console.log(TOKEN)
        const resp = await fetch( cloudUrl, {
            method: 'POST',
            body: formData,
            headers:{
                              'Authorization': 'Bearer ' + TOKEN
            }
        });

        if ( resp.ok ) {
            const cloudResp = await resp.json();





            return cloudResp;
        } else {
            throw await resp.json();
        }

    } catch (err) {
        throw err;
    }


    // return url de la imagen
}



export const fileUploadBolsa = async ( file ) => {
    APP_DEBUG_MODE   &&  console.log(file);
        const cloudUrl = baseUrl + 'filesbolsas';

        const formData = new FormData();

        formData.append('image', file );

        try {

            const TOKEN = getSessionItem("token");
            const resp = await fetch( cloudUrl, {
                method: 'POST',
                body: formData,

                  headers: new Headers({
                    'Authorization': 'Bearer ' + TOKEN,
                    'Content-Type': 'application/x-www-form-urlencoded'
                  }),
            });


            if ( resp.ok ) {
                const cloudResp = await resp.json();





                return cloudResp;
            } else {
                throw await resp.json();
            }

        } catch (err) {
            throw err;
        }


        // return url de la imagen
    }
