import { APP_DEBUG_MODE } from "../helpers";
import CategoriasServie from "../services/CategoriasServie";

export const getAllCategorias = () => {

   const categorias =    CategoriasServie.getAll()
    .then((response) => {

   let ddData = [];
        for (let i = 0; i < response.data.length; i++) {
            APP_DEBUG_MODE   &&    console.log(response.data[i]);
            var objeto =   {  value: response.data[i].id , label: response.data[i].title }
ddData.push(objeto);
          }
          APP_DEBUG_MODE   &&  console.log( ddData);

        return ddData;
    })
    .catch((err) => {
        alert("Ocurrio un error");
        APP_DEBUG_MODE   && console.log(err);
    });
    APP_DEBUG_MODE   &&console.log(categorias);
return categorias;
}
