import Swal from "sweetalert2";
import { useFormikContext, Field, ErrorMessage } from "formik";
import { memo, useEffect } from "react";
import { APP_DEBUG_MODE, useApi } from "../../helpers";
import { Progress } from "../../ui";

const FileAdd = () => {
    const formikProps = useFormikContext();
    const { file: filePost,  isLoading: fileLoading,  data: fileUrl } = useApi('file');



    useEffect(() => {
        if (fileUrl != null) {
            formikProps.setFieldValue(
                "url",
                fileUrl
            );

            Swal.close();
            Swal.fire({
                title: ':D',
                allowOutsideClick: false,
                text: 'Su imagen fue guardada correctamente',
                imageUrl: fileUrl,
                imageWidth: 400,
                imageHeight: 200,
                imageAlt: 'Custom image',
              })
        }
    }, [fileUrl]);


    const handleFileChange = (e) => {
        const file = e.target.files[0];
        if (file) {
            Swal.fire({
                title: "Cargando..",
                text: "Por favor no cierre hasta que termine de cargar...",
                allowOutsideClick: false,
                onBeforeOpen: () => {
                    Swal.showLoading();
                },
            });

            APP_DEBUG_MODE &&  console.log(file);
            const formData = new FormData();

            formData.append('image', file );
            filePost(`files`,formData );



        }
    };

   // const handlePictureClick = () => {
      //  document.querySelector("#fileSelector").click();
   // };
    return (
        <>
                        {fileLoading && <Progress />}

            <div className="mb-3">
                <label htmlFor="formFile" className="form-label">Imagen</label>
                <input className="form-control"
                  id="fileSelector"
                 type="file"
                 name="file"
                 accept="image/*"
                onChange={handleFileChange}/>
            </div>

            <Field name="url" id="url" type="hidden" />
            <ErrorMessage name="url" component="span" />
        </>
    );
};

export default memo(FileAdd);
