import { memo } from 'react'


import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet'


function Sucursales() {
  return (
    <div>

      <div className="container-fluid py-1">
        <div className="col-md-6 m-auto text-center">
          <h1 className="h1">Sucursales</h1>
          
    

           <h3><span style={{fontWeight: 'bold'}}>Lunes a Viernes</span> </h3> 	8:00 - 14:00 y 16:00 - 18:30
            <h4><span style={{fontWeight: 'bold'}}>Sabado</span> </h4> 08:00 - 14:00 
            <h4><span style={{fontWeight: 'bold'}}>Domingo</span>   </h4> CERRADO
         
        </div>
      </div>

      <section className="container py-5 " >
      <div className="card col-md-12" style={{ zIndex:1 }} >
          <div className="card-body text-center">
        <h5 className="card-title"> <button className="btn btn-primary " data-bs-toggle="modal" data-bs-target="#ModalMatriz"> Matriz El Grullo</button></h5>

          <MapContainer center={[19.807363042614707, -104.21958776191242]} zoom={15} style={{ height: 600, width: "100%" }} scrollWheelZoom={false}>
                    <TileLayer
                      attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                      url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                    />
                    <Marker position={[19.80839108171527, -104.22788637039615]}>
                      <Popup>
                      <div className='text-center'> <img src="img/logo.jpg" style={{width: 100 }}  /><br /></div>
                         
                        <div className='text-center'>Avenida Circunvalacin Poniente 10 <br /> El Grullo, Jalisco </div>
                        
                      </Popup>
                    </Marker>


                  </MapContainer>

          </div>
        </div>

        <div className="card col-md-12" style={{ zIndex:1 }} >
          <div className="card-body text-center">
          <h5 className="card-title"> <button className="btn btn-primary" data-bs-toggle="modal" data-bs-target="#ModalSucursal"> Sucursal Ayutla</button></h5>
          <MapContainer center={[20.123321354693292, -104.33975535400376]} zoom={15} style={{ height: 600, width: "100%" }} scrollWheelZoom={false}>
                <TileLayer
                  attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                  url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                />
                <Marker position={[20.123321354693292, -104.33975535400376]}>
                  <Popup>
 
                   <div className='text-center'> <img src="img/logo.jpg" style={{width: 100 }}  /></div>
                         
                        <div className='text-center'> Prolongacion 5 de mayo 344 B <br />  Ayutla, Jalisco</div>
                  </Popup>
                </Marker>


              </MapContainer>
          </div>
        </div>



            </section>

      {/* Modal Matriz*/}
      <div className="modal fade" id="ModalMatriz" tabIndex={-2} aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div className="modal-dialog modal-dialog-centered" role="document">
          <div className="modal-content text-center">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">Matriz El Grullo</h5>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
            </div>
            <div className="modal-body">

            <div className="text-center">
              <img src="img/empresa.jpg" style={{width: 400 }} alt="Aboutus" />
            </div>
            <h4 className="">Avenida Circunvalacin Poniente 10 <br /> El Grullo, Jal.<br /></h4>
            </div>
            <div className="modal-footer">


            </div>
          </div>
        </div>
      </div>

       {/* Modal Sucursal */}
       <div className="modal fade" id="ModalSucursal" tabIndex={-2} aria-labelledby="exampleModalLabel" aria-hidden="true">
         <div className="modal-dialog modal-dialog-centered" role="document">
          <div className="modal-content text-center">
            <div className="modal-header ">
              <h5 className="modal-title" id="exampleModalLabel">Sucursal Ayutla</h5>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
            </div>
            <div className="modal-body">

            <div className="text-center ">
              <img src="img/ayutla.jpg" style={{width: 400 }} alt="Aboutus" />
            </div>
            <h4 className="">Prolongacion 5 de mayo 344 B <br /> Ayutla, Jal.<br /></h4>
            </div>
            <div className="modal-footer">

            </div>
          </div>
        </div>
      </div>


    </div>





  );
}
export default memo(Sucursales);
